
// ---------------üçÄglobal--------------

*{
    /* font-family: "Open Sans", sans-serif; */  
    text-decoration: none; 

    border: 0;
    margin: 0;
    padding: 0;
  
    /* ü¶ÑmarginÏùò Í∏∞Ï§ÄÏÑ†Ïù¥ borderÎ•º Í∏∞Ï§ÄÏúºÎ°ú  ÎßåÎì§Ïñ¥Ïßê */
    box-sizing: border-box;
  
    /* ü¶Ñ  Î™®Îì† transitionÏóê Í≥µÌÜµÏúºÎ°ú ÎÑ£Ïùå. ÏùºÏùºÌûà Í∞Å elementÎßàÎã§ transitionÎÑ£ÏùÑ ÌïÑÏöîÏóÜÏùå*/
    transition: all 0.2s linear;  
    
  }
  
body {
    max-width: 1150px;
    // text-align: center;
    margin: auto;
  }

.link{
    color: inherit;
    text-decoration: none;
}

.myButton {
	box-shadow:inset 0px 1px 0px 0px #ffffff;
	background:linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
	background-color:#f9f9f9;
	border-radius:6px;
	border:1px solid #dcdcdc;
	display:inline-block;
	cursor:pointer;
	color:#666666;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;


  /* Î™®Îì† Î≤ÑÌäº width ÎòëÍ∞ôÏùÄ ÌÅ¨Í∏∞Î°ú ÏÑ§Ï†ï */
  width: 9rem;
  text-align: center; 

  /* hover-focus-activeÏàúÏÑúÎ°ú ÌïòÎ©¥ ÏóêÎü¨ ÏïàÎÇ® */
  &:hover {
      background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
      background-color:#e9e9e9;
  }
  &:active {
      position:relative;
      top:1px;
  }
}



// ---------------üçÄnavbar--------------
#navbar{    
    .wrapper{
        width: 1150px;
        height: 80px;  
        // padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;   

        .item{
            /* ü¶Ñtext ÏàòÏßÅÏ†ïÎ†¨
              display: flex;  
              align-items: center;              
            */
            display: flex;  
            align-items: center;  
            font-size: 18px;
        }

        .left{
            flex: 2;    /* ü¶Ñflex:1  */

            display: flex;
            
            // align-items: center;
            gap: 25px;
        }
        // .center{
        //     flex: 1;     /* ü¶Ñflex:1  */
        //     text-align: center;

        //     font-size: 30px;
        //     letter-spacing: 2px;
        // }
        .right{
            flex: 1;     /* ü¶Ñflex:1  */

            display: flex;
            // align-items: center;
            justify-content: center;
            gap: 25px;

            .icons{
                display: flex;
                gap: 15px;
                color: #777;
                cursor: pointer;                

                .cartIcon{
                    position: relative;
                    
                    span{
                        position: absolute;
                        right: -10px;
                        top: -10px;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        font-size: 12px;
                        width: 20px;
                        height: 20px;

                        border-radius: 50%;
                        background-color: rgb(212, 26, 26);
                        color: white;         
                        
                        
                    }
                }
            }
        }        
    }
}


