// ---------------üçÄslider--------------
#slider{
    // height: calc(100vh - 80px); /* ü¶Ñnavbar height 80pxÎßåÌÅº Î∫å */
    // width: 100vw;
    height: 50vh;
    width: 50vw;

    margin: auto;
    position: relative;
    overflow: hidden;
  
    .container{
        width: 150vw;   /* üëâ0348 slider.js image3Í∞ú Îì§Ïñ¥Í∞à ÎßåÌÅº.. 50vw X 3Í∞úÏù¥ÎØ∏ÏßÄ  */
        height: 100%;
        display: flex;
        transition: all 1s ease;      /* üëâ0348 slider.js */

        img{
            width: 50vw;    /* üëâ0348. slider.js */
            height: 100%;
            object-fit: cover;      /* ü¶Ñobject-fit: cover; */
        }
    }

    .icons{
        // width: fit-content;     /*ü¶Ñfit-content; */
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        position: absolute;     
        left: 0;
        right: 0;
        top: 50%;
        transform: translateY(-50%);    /* ÏàòÏßÅÏ†ïÎ†¨ */
     
        margin: auto;       /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */

        .icon{
            width: 50px;
            height: 50px;
            border: 1px solid #000;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;

            border-radius: 50%;
            background-color: transparent;
        }
    }
}

