// ---------------üçÄcategories--------------

#categories{
    img{
        width: 30%;
    }
    .parent {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 10px;  /* üç§gap */
        grid-row-gap: 10px;  /* üç§gap */

        div{
            position: relative;
            overflow-y: hidden;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;               
              }

            button{
                position: absolute;
                min-width: 100px;   /* ü¶Ñmin-widthÏóÜÏúºÎ©¥ Î≤ÑÌäºÏù¥ ÎÑàÎ¨¥ ÏûëÏïÑÏÑú ÎÑ£Ïùå.  */
                width: fit-content;   /* ü¶Ñwidth: fit-content; */
                height: 50px;
                padding: 10px;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                cursor: pointer;
                border: none;
                background-color: white;
                text-transform: uppercase;
                font-weight: 500;
            }
        }
        
        .div1 { grid-area: 1 / 1 / 2 / 2; }
        .div2 { grid-area: 2 / 1 / 3 / 2; }
        .div3 { grid-area: 1 / 2 / 3 / 3; }
        .div4 { grid-area: 1 / 3 / 2 / 4; }
        .div5 { grid-area: 1 / 4 / 2 / 5; }
        .div6 { grid-area: 2 / 3 / 3 / 5; }
    }        
}


