            }
        }
        .mainImg {
            flex: 5;
            border: 1px solid rgb(212, 210, 210);
            img{
                width: 100%;
                height: 800px;
                object-fit: contain;
            }
        }
    }

    .right{
        flex: 1;
        
        /*ü¶Ñdisplay: flex;, flex-direction: column;ÏùÑ ÏÇ¨Ïö©ÌïòÎäî Ïù¥Ïú† : 
         gapÏùÑ Ïì∞Í∏∞ÏúÑÌï¥ÏÑú  
         ÌïòÏúÑ ÌÉúÍ∑∏Ïóê flex: 1, flex:3 Îì±ÏúºÎ°ú Î†àÏù¥ÏïÑÏõÉ ÌïòÍ∏∞ ÏúÑÌï¥ÏÑú         
        */
