        flex-direction: column;
        gap: 30px;

        .price{
            font-size: 30px;
            font-weight: 300;
            text-align: justify;
        }
        
        p{
            font-size: 18px;
            font-weight: 300;
            text-align: justify;
        }
        .quantity{
            display: flex;
            align-items: center;
            gap: 10px;

            button{
                width: 50px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                border: none;
              }
            }
            
        .addCart{
            width: 250px;
            padding: 10px;

            display: flex;
            align-items: center;
            justify-content: center;

            gap: 20px;
            cursor: pointer;

            font-weight: 500;               
            
            background-color: #2879fe;
            color: white;                
        }

        .links{
            display: flex;
            gap: 20px;
      
            .item{
              display: flex;
